const BREAKPOINT_XS = 480;
const IS_SCROLLABLE_LEFT = 'has-scroll-left';
const IS_SCROLLABLE_RIGHT = 'has-scroll-right';
const HAS_SCROLLER = 'has-scroller';

/**
 * Returns true if device is < BREAKPOINT_XS
 *
 * @returns {boolean} - is device mobile
 */
export const isMobile = () => window.innerWidth < BREAKPOINT_XS;

/**
 * If given element is outside visible viewport, call native scrollIntoView to scroll to the element
 *
 * @param {HTMLElement} el - element to scroll to
 */
export const scrollToElement = (el: HTMLElement) => {
  const parent = el.parentNode as HTMLElement;
  const { offsetTop, scrollTop, clientHeight } = parent;
  const overTop = el.offsetTop - offsetTop < scrollTop;
  const overBottom =
    el.offsetTop - offsetTop + el.clientHeight > scrollTop + clientHeight;
  const alignWithTop = overTop && !overBottom;

  if (overTop || overBottom) {
    el.scrollIntoView(alignWithTop);
  }
};

/**
 * Toggles parent classes based on child horizontal-scroll position
 *
 * - For hiding/showing horizontal scroll arrows ยก
 *
 * @param {HTMLElement} wrapper - wrapping parent element
 * @param {HTMLElement} el - scrolling child element
 */
export const toggleHorizontalScrollArrows = (
  wrapper: HTMLElement,
  el: HTMLElement,
) => {
  const contentScrollLeft = el.scrollLeft;
  const contentWidth = el.scrollWidth;
  const contentInnerWidth = el.offsetWidth;

  if (contentWidth > contentInnerWidth) {
    wrapper.classList.add(HAS_SCROLLER);
  } else {
    wrapper.classList.remove(HAS_SCROLLER);
  }

  if (contentScrollLeft > 0) {
    wrapper.classList.add(IS_SCROLLABLE_LEFT);
  } else {
    wrapper.classList.remove(IS_SCROLLABLE_LEFT);
  }

  if (contentScrollLeft + contentInnerWidth >= contentWidth) {
    wrapper.classList.remove(IS_SCROLLABLE_RIGHT);
  } else {
    wrapper.classList.add(IS_SCROLLABLE_RIGHT);
  }
};

/**
 * get the closest parent element with a matching selector
 *
 * @param elem
 * @param selector
 * @returns {null|*}
 */
export const getClosest = (elem: any, selector: any) => {
  for (; elem && elem !== document; elem = elem.parentNode) {
    if (elem.matches(selector)) return elem;
  }
  return null;
};
